.header {
  @include flex-box-center(column);
  padding-bottom: 1em;
  h1 {
    color: $twitter-blue;
    font-size: $header-size;
  }
  svg {
    color: white;
    &:hover {
      cursor: pointer;
    }
  }
}

button.header__tweet-btn {
  padding: 1em 3em;
  border-radius: 1em;
  @extend %background-styles;
}



