.tweet {
  border: none;
  padding: 1em;
  margin: 1em;
  border-radius: 1em;
  background: white;
  width: 100%;
  &:first-of-type {
    margin: 0;
  }
  p {
    // for some reason, blablabla won't word wrap whereas actual words do.
    overflow: hidden;
  }
}



